import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOtionsPsComplexVascoSoftTokenExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-vasco-soft-token/ps-complex-vasco-soft-token.component.interface';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsLabel, IOptionsPsTemplateForm, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from 'src/app/pages/omni-base/omni-base.page';

@Component({
  selector: 'activate-soft-token-otp',
  templateUrl: './activate-soft-token-otp.page.html',
  styleUrls: ['./activate-soft-token-otp.page.scss'],
})
export class ActivateSoftTokenOtpPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  softTokenScreenVO: any = {};
  templateFormOptions: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.activateSoftToken,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            this.executionClass.deactivationConfirmation().then(() => {
              resolve(true);
            }).catch(() => {
              reject()
            })
          });
        },
        params: [this],
        executionClass: this
      },
    }

  };

  stepperOptions: IOptionsTemplateStepper = {
    isHorizontalStepper: true,
    stepperName: 'activate_soft_token_otp_stepper',
    numberOfSteps: 2,
    namesofSteps: ['activate_soft_token_otp_hints_step', 'activate_soft_token_vasco'],
    group: this.formGroup,
    submitOptions: {
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.activateSoftToken,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateVO(response));
          });
        },
        executionClass: this,
        params: [this]
      },
    },
    requestObject: this.softTokenScreenVO,
    showReport: false
  };

  activateSoftTokenDetailsPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'activate_soft_token_otp_key',
    iconName: 'document',
    expanded: true
  };

  activateSoftTokenVascoPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'activate_soft_token_otp_key',
    iconName: 'document',
    expanded: true
  };

  activateSoftTokenStepsLblOptions: IOptionsPsLabel = {
    fcName: 'activateSoftTokenSteps',
    labelKey: 'activate_soft_token_steps_key'
  };

  softTokenHintLblOptions: IOptionsPsLabel = {
    fcName: 'softTokenHint',
    labelKey: 'soft_token_otp_can_be_activated_on_3_devices_at_maximum_key'
  };

  crontoDetailsPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'cronto_details_key',
    iconName: 'document',
    expanded: true
  };

  complexVascoSoftTokenOptions: IOtionsPsComplexVascoSoftTokenExposed = {
    disableSoftTokenBtn: false,
    activateSoftTokenOptions: {
      labelKey: 'activate_soft_token_key',
      type: 'button',
      psClass: 'ps-button-activate',
      group: this.formGroup,
    },
    crontoImageOptions: {
      imageName: ''
    },
    crontoCodeOptions: {
      group: this.formGroup,
      labelKey: 'cronto_code_key',
      placeHolder: 'cronto_code_key',
      fcName: 'crontoCode'
    }
  }
  constructor(private navService: PsNavigatorService, private omniPull: OmniPullService) {
    super();
  }

  ngOnInit() {
    this.commonProv.copyObject(this.softTokenScreenVO, this.navService.getAllParams() ? this.navService.getAllParams() : {})
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.stepperOptions.stepperName + '_preview', this.stepperOptions.stepperName + '_terms'], 0);
  }
  //Edited by Marina jira-AZDB-1398; check if the soft token is enabled in ionViewDidEnter so when the user re-enter the screen the button is enabled as we disable it after the cronto image is displayed in ps-complex-soft-token
  ionViewDidEnter(): void {
    super.ionViewDidEnter();
    this.commonProv.presentLoading();
    this.omniPull.getParamValOf(CommonBussinessConstant.ENABLE_SOFT_TOKEN_OTP).then(res => {
      if (res[CommonBussinessConstant.ENABLE_SOFT_TOKEN_OTP]) {
        this.complexVascoSoftTokenOptions.disableSoftTokenBtn = false
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['activate_soft_token_btn'], 0);
      }
    }).finally(() => {
      this.commonProv.dismissLoading();
    })
  }
  //added by Marina jira-AZDB-1400; add the cronto code value to "value" property as requested by Hasan F.
  updateVO() {
    this.softTokenScreenVO['value'] = this.softTokenScreenVO[this.complexVascoSoftTokenOptions.crontoCodeOptions.fcName];
  }

}
